import React, {
  ReactNode,
  FC
} from 'react';
import { css } from '@emotion/react';

export interface ButtonProps {
  children: ReactNode;
  onClick?: () => void;
}

const Button: FC<ButtonProps> = ({
  children,
  onClick
}: ButtonProps) => {
  const buttonStyles = css({
    width: 50,
    height: 50,
    backgroundColor: 'red',
  });
  return (
    <button
      onClick={onClick}
      css={css`
        ${buttonStyles}
      `}
    >
      {children}
    </button>
  )
}

export default Button;
